
.button-bar {
  position: absolute;
  width: 100%;
  height: 100%;
}
.button-bar__inner {
  position: absolute;
}
.button-bar__inner--bottom {
  bottom: 0;
}
.button-bar__button {
  color: rgba(0, 0, 0, 0.2);
  display: block;
  width: 26px;
  height: 26px;
  padding: 2px;
  margin: 3px 0;
}
.app--dark .button-bar__button {
    color: rgba(255, 255, 255, 0.15);
}
.button-bar__button:active, .button-bar__button:focus, .button-bar__button:hover {
    color: rgba(0, 0, 0, 0.2);
}
.app--dark .button-bar__button:active, .app--dark .button-bar__button:focus, .app--dark .button-bar__button:hover {
      color: rgba(255, 255, 255, 0.15);
      background-color: rgba(255, 255, 255, 0.1);
}
.button-bar__button--on {
  color: rgba(0, 0, 0, 0.4);
}
.app--dark .button-bar__button--on {
    color: rgba(255, 255, 255, 0.4);
}
.button-bar__button--on:active, .button-bar__button--on:focus, .button-bar__button--on:hover {
    color: rgba(0, 0, 0, 0.4);
}
.app--dark .button-bar__button--on:active, .app--dark .button-bar__button--on:focus, .app--dark .button-bar__button--on:hover {
      color: rgba(255, 255, 255, 0.4);
}



