
.current-discussion {
  position: absolute;
  right: 0;
  bottom: 0;
  border-top: 2px solid;
}
.current-discussion .sticky-comment {
    position: relative;
}
.current-discussion__inner {
  position: relative;
  font-size: 16px;
  background-color: rgba(255, 150, 50, 0.15);
  max-height: 130px;
  /* 3 lines max */
  overflow: hidden;
}
.current-discussion__buttons {
  padding: 4px 4px 0;
}
.current-discussion__button {
  width: 30px;
  height: 28px;
  padding: 2px;
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
  color: rgba(0, 0, 0, 0.5);
}
.current-discussion__button:active, .current-discussion__button:focus, .current-discussion__button:hover {
    color: rgba(0, 0, 0, 0.75);
}
.current-discussion__button--remove {
  /* Make the trash a bit smaller */
  padding: 3px;
}
.current-discussion__button--rotate {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.current-discussion__text {
  padding: 10px;
}
.current-discussion__text span {
    padding: 0.2em 0;
    background-color: #ffa147;
    cursor: pointer;
}
.current-discussion__text span .app--dark {
      background-color: #e98a30;
}



