
.history {
  padding: 5px 5px 50px;
}
.history__button {
  font-size: 14px;
  margin-top: 0.5em;
}
.history__spacer {
  position: relative;
  height: 40px;
}
.history__spacer::before {
    content: '';
    position: absolute;
    height: 100%;
    top: 0;
    left: 24px;
    border-left: 2px dotted rgba(128, 128, 128, 0.2);
}
.history__spacer--last {
  height: 20px;
}
.revision__button {
  text-align: left;
  padding: 15px;
  height: auto;
  text-transform: none;
  position: relative;
}
.revision__button::before {
    content: '';
    position: absolute;
    height: 100%;
    top: 0;
    left: 24px;
    border-left: 2px solid rgba(128, 128, 128, 0.2);
}
.revision__button:active::before, .revision__button:focus::before, .revision__button:hover::before {
    display: none;
}
.revision:first-child .revision__button::before {
    height: 67%;
    top: 33%;
}
.revision__icon {
  height: 20px;
  width: 20px;
  margin-right: 12px;
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
  border-radius: 2px;
  overflow: hidden;
  position: relative;
}
.revision__header {
  font-size: 15px;
  width: 100%;
}
.revision__created {
  font-size: 0.75em;
  opacity: 0.5;
}
.layout--revision .cledit-section *,
.layout--revision .cl-preview-section * {
  color: rgba(0, 0, 0, 0.13) !important;
}
.app--dark .layout--revision .cledit-section *, .app--dark
  .layout--revision .cl-preview-section * {
    color: rgba(255, 255, 255, 0.13) !important;
}
.layout--revision .cledit-section .revision-diff {
  color: rgba(0, 0, 0, 0.8) !important;
}
.app--dark .layout--revision .cledit-section .revision-diff {
    color: rgba(255, 255, 255, 0.8) !important;
}
.layout--revision .cl-preview-section .revision-diff {
  color: rgba(0, 0, 0, 0.75) !important;
}
.app--dark .layout--revision .cl-preview-section .revision-diff {
    color: rgba(255, 255, 255, 0.75) !important;
}
.layout--revision .revision-diff {
  padding: 0.25em 0;
}
.layout--revision .revision-diff.revision-diff--insert {
    background-color: #ffd5ad;
}
.layout--revision .revision-diff.revision-diff--delete {
    background-color: #ffada0;
    text-decoration: line-through;
}



