
.modal__inner-1--sponsor {
  max-width: 380px;
}
.paypal-option {
  text-align: center;
  padding: 10px;
  height: auto;
  font-size: 2.3em;
  margin: 0.75rem 0;
  line-height: 1.2;
  text-transform: none;
}
.paypal-option span {
    display: inline-block;
    font-size: 0.75rem;
    opacity: 0.5;
    white-space: normal;
    line-height: 1.5;
}
.paypal-option .paypal-option__offer {
    float: right;
    font-size: 0.6rem;
    font-weight: 600;
    padding: 0.1em 0.2em;
    background-color: #dd2000;
    border-radius: 3px;
    color: #fff;
    margin-left: -0.5em;
}



