
@charset "UTF-8";
.explorer-node--drag-target {
  background-color: rgba(0, 128, 255, 0.2);
}
.explorer-node__item {
  position: relative;
  cursor: pointer;
  font-size: 14px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-right: 5px;
}
.explorer-node--selected > .explorer-node__item {
    background-color: rgba(0, 0, 0, 0.2);
}
.explorer__tree:focus .explorer-node--selected > .explorer-node__item {
      background-color: #39f;
      color: #fff;
}
.explorer__tree--new-item .explorer-node__item {
    opacity: 0.33;
}
.explorer-node__item .explorer-node__location {
    float: right;
    width: 18px;
    height: 18px;
    margin: 2px 1px;
}
.explorer-node__item--folder::before,
.explorer-node__item-editor--folder::before,
.explorer-node__new-child--folder::before {
  content: '';
  position: absolute;
  margin-left: -13px;
}
.explorer-node--open > .explorer-node__item--folder::before, .explorer-node--open >
  .explorer-node__item-editor--folder::before, .explorer-node--open >
  .explorer-node__new-child--folder::before {
    content: '';
}
.explorer-node__item-editor,
.explorer-node__new-child {
  padding: 1px 10px;
}
.explorer-node__item-editor .text-input,
  .explorer-node__new-child .text-input {
    font-size: 14px;
    padding: 2px;
    height: 25px;
}



