
.context-menu {
  position: absolute;
  width: 100%;
  height: 100%;
  font-size: 14px;
  line-height: 18px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.context-menu__inner {
  position: absolute;
  background-color: #ebebeb;
  border-radius: 5px;
  padding: 5px 0;
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.16), 0 3px 10px 1px rgba(0, 0, 0, 0.12);
}
.context-menu__item {
  display: block;
  color: #333;
  text-decoration: none;
  padding: 0 25px;
}
a.context-menu__item:active, a.context-menu__item:focus, a.context-menu__item:hover {
  background-color: #338dfc;
  color: #fff;
}
.context-menu__item--disabled {
  color: #aaa;
}
.context-menu__separator {
  border-top: 2px solid #dcdcdd;
  margin: 5px 0;
}



