
.find-replace {
  padding: 0 35px 0 25px;
}
.find-replace__row {
  margin: 10px 0;
}
.find-replace__button {
  font-size: 15px;
  padding: 0 8px;
  line-height: 28px;
  height: 28px;
}
.find-replace__button--find-option {
  padding: 0;
  width: 28px;
  font-weight: 600;
  letter-spacing: -0.025em;
  color: rgba(0, 0, 0, 0.25);
  text-transform: none;
}
.find-replace__button--find-option:active, .find-replace__button--find-option:focus, .find-replace__button--find-option:hover {
    color: rgba(0, 0, 0, 0.25);
}
.find-replace__button--on {
  color: rgba(0, 0, 0, 0.67);
}
.find-replace__button--on:active, .find-replace__button--on:focus, .find-replace__button--on:hover {
    color: rgba(0, 0, 0, 0.67);
}
.find-replace__text-input {
  border: 1px solid transparent;
  padding: 2px 5px;
  height: 32px;
}
.find-replace__text-input:focus {
    border-color: #0c93e4;
}
.find-replace__close-button {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 25px;
  height: 25px;
  padding: 2px;
  color: rgba(0, 0, 0, 0.5);
}
.find-replace__close-button:active, .find-replace__close-button:focus, .find-replace__close-button:hover {
    color: rgba(0, 0, 0, 0.75);
}
.find-replace__find-stats {
  text-align: right;
  font-size: 0.75em;
  opacity: 0.5;
}
.find-replace-highlighting {
  background-color: #ff0;
  color: rgba(0, 0, 0, 0.8) !important;
}
.find-replace-selection {
  background-color: #ff9632;
}



