
.tour {
  position: absolute;
  top: 0;
  left: 0;
}
.tour-step {
  position: absolute;
}
.tour-step__inner {
  position: absolute;
  background-color: #f6dcc3;
  padding: 1.5em 1em 1em;
  font-size: 0.9em;
  line-height: 1.33;
  width: 220px;
  text-align: center;
  border-radius: 2px;
}
.tour-step__inner h2 {
    margin: 0;
}
.tour-step__inner h2::after {
      display: none;
}
.tour-step__inner .icon,
  .tour-step__inner .icon-provider {
    width: 1.25em;
    height: 1.25em;
    vertical-align: bottom;
    display: inline-block;
}
.tour-step__inner::before {
    content: '';
    position: absolute;
}
.tour-step--welcome .tour-step__inner,
  .tour-step--end .tour-step__inner {
    left: -110px;
    top: 36px;
    border-bottom-right-radius: 0;
}
.tour-step--welcome .tour-step__inner::before,
    .tour-step--end .tour-step__inner::before {
      bottom: -10px;
      right: 0;
      border-top: 10px solid #f6dcc3;
      border-left: 10px solid transparent;
}
.tour-step--editor .tour-step__inner,
  .tour-step--menu .tour-step__inner {
    right: 15px;
    border-top-right-radius: 0;
}
.tour-step--editor .tour-step__inner::before,
    .tour-step--menu .tour-step__inner::before {
      top: 0;
      right: -10px;
      border-top: 10px solid #f6dcc3;
      border-right: 10px solid transparent;
}
.tour-step--explorer .tour-step__inner {
    left: 15px;
    border-top-left-radius: 0;
}
.tour-step--explorer .tour-step__inner::before {
      top: 0;
      left: -10px;
      border-top: 10px solid #f6dcc3;
      border-left: 10px solid transparent;
}
.tour-step__button-bar {
  text-align: right;
}



