
.modal {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(160, 160, 160, 0.5);
  overflow: auto;
}
.modal hr {
    margin: 0.5em 0;
}
.modal__inner-1 {
  margin: 0 auto;
  width: 100%;
  min-width: 320px;
  max-width: 480px;
}
.modal__inner-2 {
  margin: 40px 10px 100px;
  background-color: #f8f8f8;
  padding: 40px 50px 30px;
  border-radius: 2px;
  position: relative;
  overflow: hidden;
}
.modal__inner-2::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background-image: linear-gradient(to left, #ffd700, #ffd700 23%, #a5c700 27%, #a5c700 48%, #ff8a00 52%, #ff8a00 73%, #66aefd 77%);
}
.modal__inner-2::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background-image: linear-gradient(to right, #ffd700, #ffd700 23%, #a5c700 27%, #a5c700 48%, #ff8a00 52%, #ff8a00 73%, #66aefd 77%);
}
.modal__content > :first-child,
.modal__content > .modal__image:first-child + * {
  margin-top: 0;
}
.modal__image {
  float: left;
  width: 64px;
  height: 64px;
  margin: 1.5em 1.5em 0.5em 0;
}
.modal__image + *::after {
    content: '';
    display: block;
    clear: both;
}
.modal__title {
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 1.4;
  margin-top: 2.5rem;
}
.modal__sub-title {
  opacity: 0.5;
  font-size: 0.75rem;
  margin-bottom: 1.5rem;
}
.modal__error {
  color: #de2c00;
}
.modal__info {
  background-color: rgba(255, 150, 50, 0.15);
  border-radius: 2px;
  margin: 1.2em 0;
  padding: 0.75em 1.25em;
  font-size: 0.95em;
  line-height: 1.6;
}
.modal__info pre {
    line-height: 1.5;
}
.modal__button-bar {
  margin-top: 1.75rem;
  text-align: right;
}
.form-entry {
  margin: 1em 0;
}
.form-entry__label {
  display: block;
  font-size: 0.9rem;
  color: #a0a0a0;
}
.form-entry--focused .form-entry__label {
    color: #0974b4;
}
.form-entry--error .form-entry__label {
    color: #dd2000;
}
.form-entry__label-info {
  font-size: 0.75rem;
}
.form-entry__field {
  border: 1px solid #d8d8d8;
  border-radius: 2px;
  position: relative;
  overflow: hidden;
}
.form-entry--focused .form-entry__field {
    border-color: #0c93e4;
}
.form-entry--error .form-entry__field {
    border-color: #f31;
}
.form-entry__actions {
  text-align: right;
  margin: 0.25em;
}
.form-entry__button {
  width: 38px;
  height: 38px;
  padding: 6px;
  display: inline-block;
  background-color: transparent;
  opacity: 0.75;
}
.form-entry__button:active, .form-entry__button:focus, .form-entry__button:hover {
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.1);
}
.form-entry__radio,
.form-entry__checkbox {
  margin: 0.25em 1em;
}
.form-entry__radio input,
  .form-entry__checkbox input {
    margin-right: 0.25em;
}
.form-entry__info {
  font-size: 0.75em;
  opacity: 0.5;
  line-height: 1.4;
  margin: 0.25em 0;
}
.tabs {
  border-bottom: 1px solid rgba(128, 128, 128, 0.2);
  margin: 1em 0 2em;
}
.tabs::after {
    content: '';
    display: block;
    clear: both;
}
.tabs__tab {
  width: 50%;
  float: left;
  text-align: center;
  line-height: 1.4;
  font-weight: 400;
  font-size: 1.1em;
}
.tabs__tab > a {
  width: 100%;
  text-decoration: none;
  padding: 0.67em 0.33em;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  border-top-left-radius: 2px;
  border-top-right-radius: 2px;
  color: #0c93e4;
}
.tabs__tab > a:hover, .tabs__tab > a:focus {
    background-color: rgba(0, 0, 0, 0.05);
}
.tabs__tab--active > a {
  border-bottom: 2px solid #0c93e4;
  color: inherit;
}



