
.modal__inner-1--sync-management {
  max-width: 560px;
}
.sync-entry {
  padding: 0.5rem 0.25rem;
  border-bottom: 1px solid rgba(128, 128, 128, 0.2);
}
.sync-entry:last-child {
    border-bottom: none;
}
.sync-entry__icon {
  height: 30px;
  width: 30px;
  margin-right: 0.75rem;
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
}
.sync-entry__description {
  opacity: 0.5;
  line-height: 1.4;
  font-size: 0.9em;
  width: 100%;
  overflow: hidden;
}
.sync-entry__buttons {
  margin-left: 0.75rem;
}
.sync-entry__button {
  width: 38px;
  height: 38px;
  padding: 6px;
  background-color: transparent;
  opacity: 0.75;
}
.sync-entry__button:active, .sync-entry__button:focus, .sync-entry__button:hover {
    opacity: 1;
}



