
.side-bar {
  overflow: hidden;
  height: 100%;
}
.side-bar hr {
    margin: 10px 40px;
    display: none;
    border-top: 1px solid rgba(128, 128, 128, 0.2);
}
.side-bar * + hr {
    display: block;
}
.side-bar hr + hr {
    display: none;
}
.side-bar__inner {
  position: relative;
  height: 100%;
}
.side-bar__panel {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.side-bar__panel::after {
    content: '';
    display: block;
    height: 40px;
}
.side-bar__panel--hidden {
  left: 1000px;
}
.side-bar__panel--menu {
  padding: 10px;
}
.side-bar__panel--help {
  padding: 0 10px 0 20px;
}
.side-bar__panel--help pre {
    font-size: 0.9em;
    -webkit-font-variant-ligatures: no-common-ligatures;
            font-variant-ligatures: no-common-ligatures;
    line-height: 1.25;
    white-space: pre-wrap;
    word-break: break-word;
    word-wrap: break-word;
}
.side-bar__panel--help .code,
  .side-bar__panel--help .img,
  .side-bar__panel--help .imgref,
  .side-bar__panel--help .cl-toc {
    background-color: rgba(0, 0, 0, 0.05);
}
.side-bar__info {
  padding: 10px;
  margin: -10px -10px 10px;
  background-color: rgba(255, 150, 50, 0.15);
}
.side-bar__info p {
    margin: 10px;
    line-height: 1.4;
}



