
.preview,
.preview__inner-1 {
  position: absolute;
  width: 100%;
  height: 100%;
}
.preview__inner-1 {
  overflow: auto;
}
.preview__inner-2 {
  margin: 0;
}
.preview__inner-2 > :first-child > :first-child {
  margin-top: 0;
}
.preview__corner {
  position: absolute;
  top: 0;
  right: 0;
}
.preview__corner::before {
    content: '';
    position: absolute;
    right: 0;
    border-top: 110px solid rgba(0, 0, 0, 0.075);
    border-left: 110px solid transparent;
    pointer-events: none;
}
.app--dark .preview__corner::before {
      border-top-color: rgba(255, 255, 255, 0.075);
}
.preview__button {
  position: absolute;
  top: 15px;
  right: 15px;
  width: 40px;
  height: 40px;
  padding: 5px;
  color: rgba(0, 0, 0, 0.25);
}
.app--dark .preview__button {
    color: rgba(255, 255, 255, 0.25);
}
.preview__button:active, .preview__button:focus, .preview__button:hover {
    color: rgba(0, 0, 0, 0.33);
    background-color: transparent;
}
.app--dark .preview__button:active, .app--dark .preview__button:focus, .app--dark .preview__button:hover {
      color: rgba(255, 255, 255, 0.33);
}



