
.modal__inner-1--workspace-management {
  max-width: 560px;
}
.workspace-entry {
  text-align: left;
  padding-left: 10px;
  margin: 15px 0;
  height: auto;
  font-size: 17px;
  line-height: 1.5;
  text-transform: none;
}
.workspace-entry:last-child {
    border-bottom: none;
}
.workspace-entry span {
    text-overflow: ellipsis;
    overflow: hidden;
}
.workspace-entry__icon {
  height: 20px;
  width: 20px;
  margin-right: 12px;
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
}
.workspace-entry__description {
  width: 100%;
  word-wrap: break-word;
  overflow: hidden;
}
.workspace-entry__name {
  overflow: hidden;
  font-weight: bold;
}
.workspace-entry__url {
  opacity: 0.5;
  font-size: 0.75em;
}
.workspace-entry__buttons {
  margin-left: 0.75rem;
}
.workspace-entry__button {
  width: 36px;
  height: 36px;
  padding: 6px;
  background-color: transparent;
  opacity: 0.75;
}
.workspace-entry__button:active, .workspace-entry__button:focus, .workspace-entry__button:hover {
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.1);
}



