
.layout {
  position: absolute;
  width: 100%;
  height: 100%;
}
.layout__panel {
  position: relative;
  width: 100%;
  height: 100%;
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
  overflow: hidden;
}
.layout__panel--navigation-bar {
  background-color: #2c2c2c;
}
.layout__panel--status-bar {
  background-color: #007acc;
}
.layout__panel--editor {
  background-color: #fff;
}
.app--dark .layout__panel--editor {
    background-color: #1e1e1e;
}
.layout__panel--editor .gutter__background,
  .layout__panel--editor .comment-list__current-discussion,
  .layout__panel--editor .sticky-comment,
  .layout__panel--editor .current-discussion {
    background-color: #eeeeee;
    border-color: #fff;
}
.app--dark .layout__panel--editor .gutter__background, .app--dark
    .layout__panel--editor .comment-list__current-discussion, .app--dark
    .layout__panel--editor .sticky-comment, .app--dark
    .layout__panel--editor .current-discussion {
      background-color: #2d2d2d;
      border-color: #1e1e1e;
}
.layout__panel--preview,
.layout__panel--button-bar {
  background-color: #f3f3f3;
}
.app--dark .layout__panel--preview, .app--dark
  .layout__panel--button-bar {
    background-color: #252525;
}
.layout__panel--preview .gutter__background,
.layout__panel--preview .comment-list__current-discussion,
.layout__panel--preview .sticky-comment,
.layout__panel--preview .current-discussion {
  background-color: #e3e3e3;
  border-color: #f3f3f3;
}
.layout__panel--explorer,
.layout__panel--side-bar {
  background-color: #ddd;
}
.layout__panel--find-replace {
  background-color: #e6e6e6;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 300px;
  height: auto;
  border-top-right-radius: 2px;
}



