
.toc__inner {
  position: relative;
  color: rgba(0, 0, 0, 0.75);
  cursor: pointer;
  font-size: 9px;
  padding: 10px 20px 40px;
  white-space: nowrap;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.toc__inner * {
    font-weight: inherit;
    pointer-events: none;
}
.toc__inner .cl-toc-section h1::after,
  .toc__inner .cl-toc-section h2::after {
    display: none;
}
.toc__inner .cl-toc-section h1 {
    margin: 1rem 0;
}
.toc__inner .cl-toc-section h2 {
    margin: 0.5rem 0;
    margin-left: 8px;
}
.toc__inner .cl-toc-section h3 {
    margin: 0.33rem 0;
    margin-left: 16px;
}
.toc__inner .cl-toc-section h4 {
    margin: 0.22rem 0;
    margin-left: 24px;
}
.toc__inner .cl-toc-section h5 {
    margin: 0.11rem 0;
    margin-left: 32px;
}
.toc__inner .cl-toc-section h6 {
    margin: 0;
    margin-left: 40px;
}
.toc__mask {
  position: absolute;
  left: 0;
  width: 100%;
  height: 35px;
  background-color: rgba(255, 255, 255, 0.2);
  pointer-events: none;
}



