
.comment-list {
  position: absolute;
  right: 0;
  font-size: 15px;
}
.comment--last,
.comment-list__current-discussion {
  position: absolute;
  width: 100%;
  padding-top: 10px;
}
.comment-list__current-discussion {
  border-top: 2px solid;
  border-bottom: 2px solid;
}
.comment-list--top .comment-list__current-discussion {
    border-bottom-color: transparent;
}
.comment-list--bottom .comment-list__current-discussion {
    border-top-color: transparent;
}

/* use div selector to avoid collision with Prism */
div.comment {
  padding: 5px 10px 10px;
}
.comment--last {
  opacity: 0.33;
  cursor: pointer;
}
.comment--last * {
    pointer-events: none;
}
.comment--last:hover, .comment--last.comment--hover {
    opacity: 0.5;
}
.comment__header {
  font-size: 0.75em;
  padding-bottom: 0.25em;
}
.comment__user-image {
  height: 20px;
  width: 20px;
  border-radius: 2px;
  overflow: hidden;
  margin-right: 5px;
}
.comment:hover .comment__user-image {
    display: none;
}
.sticky-comment .comment:hover .comment__user-image {
      display: block;
}
.comment--new:hover .comment__user-image,
  .comment--last:hover .comment__user-image {
    display: block;
}
.comment__remove-button {
  height: 20px;
  width: 20px;
  padding: 1px;
  color: rgba(0, 0, 0, 0.33);
  margin-right: 5px;
  display: none;
}
.comment__remove-button:active, .comment__remove-button:focus, .comment__remove-button:hover {
    color: rgba(0, 0, 0, 0.5);
}
.comment:hover .comment__remove-button {
    display: block;
}
.sticky-comment .comment:hover .comment__remove-button {
      display: none;
}
.comment--last:hover .comment__remove-button {
    display: none;
}
.comment__created {
  opacity: 0.5;
}
.comment__buttons {
  padding: 10px 5px 0;
}
.comment__button {
  padding: 0 8px;
  line-height: 28px;
  height: 28px;
}
.comment__text {
  position: relative;
}
.comment__text::before {
    content: '';
    position: absolute;
    bottom: -8px;
    right: 0;
    border-top: 8px solid #fff;
    border-left: 8px solid transparent;
}
.app--dark .comment__text::before {
      border-top-color: #1e1e1e;
}
.comment__text h1,
  .comment__text h2,
  .comment__text h3,
  .comment__text h4,
  .comment__text h5,
  .comment__text h6 {
    font-size: inherit;
}
.comment__text h1,
  .comment__text h2,
  .comment__text h3,
  .comment__text h4,
  .comment__text h5,
  .comment__text h6,
  .comment__text p,
  .comment__text blockquote,
  .comment__text pre,
  .comment__text ul,
  .comment__text ol,
  .comment__text dl {
    margin: 0.25em 0;
}
.comment__text pre {
    -webkit-font-variant-ligatures: no-common-ligatures;
            font-variant-ligatures: no-common-ligatures;
    white-space: pre-wrap;
    word-break: break-word;
    word-wrap: break-word;
    caret-color: #000;
}
.comment__text img {
    max-width: 100%;
}
.comment__text .table-wrapper {
    max-width: 100%;
    overflow: auto;
}
.comment__text-inner {
  min-height: 37px;
  max-height: 200px;
  overflow: auto;
  padding: 1px 8px;
  background-color: #fff;
  border: 1px solid transparent;
  border-radius: 2px;
  border-bottom-right-radius: 0;
}
.app--dark .comment__text-inner {
    background-color: #1e1e1e;
}
.comment__text-inner .markdown-highlighting {
    padding: 5px 0;
    margin: 0;
}



