
.navigation-bar {
  position: absolute;
  width: 100%;
  height: 100%;
  padding-top: 4px;
  overflow: hidden;
}
.navigation-bar__hidden {
  display: none;
}
.navigation-bar__inner--left {
  float: left;
}
.navigation-bar__inner--left.navigation-bar__inner--button {
    margin-right: 12px;
}
.navigation-bar__inner--right {
  float: right;
  /* prevent from seeing wrapped pagedownButtons */
  margin-bottom: 20px;
}
.navigation-bar__inner--button {
  margin: 0 4px;
}
.navigation-bar__inner--edit-pagedownButtons {
  margin-left: 15px;
}
.navigation-bar__inner--edit-pagedownButtons .navigation-bar__button,
  .navigation-bar__inner--edit-pagedownButtons .navigation-bar__spacer {
    float: left;
}
.navigation-bar__inner--title * {
  -webkit-box-flex: 0;
      -ms-flex: none;
          flex: none;
}
.navigation-bar__button,
.navigation-bar__spacer {
  height: 36px;
  padding: 0 4px;
  /* prevent from seeing wrapped pagedownButtons */
  margin-bottom: 20px;
}
.navigation-bar__button {
  width: 34px;
  padding: 0 7px;
  transition: opacity 0.25s;
}
.navigation-bar__inner--button .navigation-bar__button {
    padding: 0 4px;
    width: 38px;
}
.navigation-bar__inner--button .navigation-bar__button.navigation-bar__button--stackedit {
      opacity: 0.85;
}
.navigation-bar__inner--button .navigation-bar__button.navigation-bar__button--stackedit:active, .navigation-bar__inner--button .navigation-bar__button.navigation-bar__button--stackedit:focus, .navigation-bar__inner--button .navigation-bar__button.navigation-bar__button--stackedit:hover {
        opacity: 1;
}
.navigation-bar__button--revision {
  width: 38px;
}
.navigation-bar__button--revision:first-child {
    margin-left: 10px;
}
.navigation-bar__button--revision:last-child {
    margin-right: 10px;
}
.navigation-bar__button--restore {
  width: auto;
}
.navigation-bar__title {
  margin: 0 4px;
  font-size: 21px;
}
.layout--revision .navigation-bar__title {
    position: absolute;
    left: -9999px;
}
.navigation-bar__title,
.navigation-bar__button {
  display: inline-block;
  color: #b9b9b9;
  background-color: transparent;
}
.navigation-bar__button--sync,
.navigation-bar__button--publish {
  padding: 0 6px;
  margin: 0 5px;
}
.navigation-bar__button[disabled], .navigation-bar__button[disabled]:active, .navigation-bar__button[disabled]:focus, .navigation-bar__button[disabled]:hover {
  color: #b9b9b9;
}
.navigation-bar__title--input:active, .navigation-bar__title--input:focus, .navigation-bar__title--input:hover,
.navigation-bar__button:active,
.navigation-bar__button:focus,
.navigation-bar__button:hover {
  color: #fff;
  background-color: rgba(255, 255, 255, 0.1);
}
.navigation-bar__button--location {
  width: 20px;
  height: 20px;
  border-radius: 10px;
  padding: 2px;
  margin-top: 8px;
  opacity: 0.5;
  background-color: rgba(255, 255, 255, 0.2);
}
.navigation-bar__button--location:active, .navigation-bar__button--location:focus, .navigation-bar__button--location:hover {
    opacity: 1;
    background-color: rgba(255, 255, 255, 0.2);
}
.navigation-bar__button--blink {
  -webkit-animation: blink 1s linear infinite;
          animation: blink 1s linear infinite;
}
.navigation-bar__title--fake {
  position: absolute;
  left: -9999px;
  width: auto;
  white-space: pre-wrap;
}
.navigation-bar__title--text {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.navigation-bar--editor .navigation-bar__title--text {
    display: none;
}
.navigation-bar__title--input,
.navigation-bar__inner--edit-pagedownButtons {
  display: none;
}
.navigation-bar--editor .navigation-bar__title--input, .navigation-bar--editor
  .navigation-bar__inner--edit-pagedownButtons {
    display: block;
}
.navigation-bar__button {
  display: none;
}
.navigation-bar__inner--button .navigation-bar__button,
  .navigation-bar--editor .navigation-bar__button {
    display: inline-block;
}
.navigation-bar__button--revision {
  display: inline-block;
}
.navigation-bar__title--input {
  cursor: pointer;
}
.navigation-bar__title--input.navigation-bar__title--focus {
    cursor: text;
}
.navigation-bar--light .navigation-bar__title--input {
    display: none;
}
.navigation-bar__spinner {
  width: 24px;
  margin: 7px 0 0 8px;
}
.navigation-bar__spinner .icon {
    width: 24px;
    height: 24px;
    color: rgba(255, 51, 17, 0.5);
}
.spinner {
  width: 20px;
  height: 20px;
  display: block;
  position: relative;
  border: 2px solid rgba(185, 185, 185, 0.5);
  border-radius: 50%;
  margin: 2px;
}
.spinner::before, .spinner::after {
    content: "";
    position: absolute;
    display: block;
    width: 2px;
    background-color: #b9b9b9;
    border-radius: 1px;
    -webkit-transform-origin: 50% 0;
            transform-origin: 50% 0;
}
.spinner::before {
    height: 4px;
    left: 7px;
    top: 50%;
    -webkit-animation: spin 3000ms linear infinite;
            animation: spin 3000ms linear infinite;
}
.spinner::after {
    height: 6px;
    left: 7px;
    top: 50%;
    -webkit-animation: spin 750ms linear infinite;
            animation: spin 750ms linear infinite;
}
@-webkit-keyframes spin {
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes spin {
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@-webkit-keyframes blink {
50% {
    opacity: 1;
}
}
@keyframes blink {
50% {
    opacity: 1;
}
}



